const Home = resolve => {
    require.ensure(['./views/list.vue'], () => {
        resolve(require('./views/list.vue'));
    });
};
const Topic = resolve => {
    require.ensure(['./views/topic.vue'], () => {
        resolve(require('./views/topic.vue'));
    });
};
const Login = resolve => {
    require.ensure(['./views/login.vue'], () => {
        resolve(require('./views/login.vue'));
    });
};
const Add = resolve => {
    require.ensure(['./views/add.vue'], () => {
        resolve(require('./views/add.vue'));
    });
};
const User = resolve => {
    require.ensure(['./views/user.vue'], () => {
        resolve(require('./views/user.vue'));
    });
};
const Info = resolve => {
    require.ensure(['./views/info.vue'], () => {
        resolve(require('./views/info.vue'));
    });
};
const routers= [{ 
	path: '/', 
	name:'list',
	component:Home
},{
	path: '/topic/:id', 
	name:'topic',
	component:Topic
},{
	path: '/add', 
	name:'add',
	component:Add,
	meta: { requiresAuth: true }
},
{
	path: '/login', 
	name:'login',
	component:Login
},
{
	path: '/user/:loginname', 
	name:'user',
	component:User
},
{
	path: '/info', 
	name:'info',
	component:Info
},
{
	path: '/about', 
	name:'about',
	component(resolve){
		require.ensure(['./views/about.vue'], () => {
	        resolve(require('./views/about.vue'));
	    });
	}
},
{
	path:'/collection',
	name:'collection',
	component(resolve){
		require.ensure(['./views/collection.vue'], () => {
	        resolve(require('./views/collection.vue'));
	    });
	}
}
]
export default routers;


