<template>
	<div class="toTop" @click="toTop" v-show="show">
		<mu-icon value="publish"/>
	</div>
</template>

<script>
	import $ from 'webpack-zepto';
  	export default {
    	name: 'toTop',
    	props:['page'],
    	components: {
    	},
    	filters :{
    		
    	},
    	data () {
	      	return {
	      		show:false,
	      		target:null
	      	}
    	},
	    mounted () {
	    	if(this.page=='list'){
	    		this.target='.demo-infinite-container';
	    	}else{
	    		this.target=window
	    	}
            $(this.target).on('scroll', () => {
                if ($(this.target).scrollTop() > 200) {
                    this.show = true;
                } else {
                    this.show = false;
                }
            });
	    },
	    methods:{
			toTop(){
				$(this.target).scrollTop(0);
				this.show = false;
				return false;
			}
	    },
	    watch:{
	    	
	    }
  	}
</script>

<style scoped lang="">
	.toTop{
		width: 50px;
		height: 50px;
		background: rgba(3,169,255,.8);
		position: fixed;
		bottom: 40px;
		right: 40px;
		border-radius: 100%;
		color: #fff;
		text-align: center;
		line-height: 78px;
		z-index: 5555;
	}
	.toTop .mu-icon{
		font-size: 40px;
	}

</style>




